@charset "utf-8";

/* placeholder */
textarea::-webkit-input-placeholder,
input::-webkit-input-placeholder{font-size:16px;color:#000;opacity:0.2;font-weight:700;letter-spacing:-1px;line-height:25px;}
textarea::-moz-placeholder,
input::-moz-placeholder{font-size:16px;color:#000;opacity:0.2;font-weight:700;letter-spacing:-1px;line-height:25px;}
textarea:-ms-input-placeholder,
input:-ms-input-placeholder{font-size:16px;color:#000;opacity:0.2;font-weight:700;letter-spacing:-1px;line-height:25px;}
textarea:-moz-placeholder,
input:-moz-placeholder{font-size:16px;color:#000;opacity:0.2;font-weight:700;letter-spacing:-1px;line-height:25px;}



/*form*/
input[type="text"], input[type="number"], input[type="password"], input[type="date"],input[type="tel"], input[type="search"]{
	border:1px solid #fff;
	border-bottom-color:#c2c2c2;
	height:40px;
	line-height:1;
	font-family:"Noto Sans KR";
	font-size:16px;
	color:#000;
	padding:5.5px 0;
	width:100%;
	background-color:#fff;
	font-weight:700;
	-webkit-appearance:none;
	-moz-appearance:none;
	appearance:none;
	caret-color:#3380ff;
}
/*textarea*/
textarea{
	display:block;
	font-family:"Noto Sans KR";
	width:100%;
	height:auto;
	border:1px solid #fff;
	border-bottom-color:#c2c2c2;
	resize:none;
	font-size:16px;
	color:#000;
	padding:7px 0;
	line-height:1.5;
	caret-color:#3380ff;
	min-height:75px;
	background:#f5f5f5;
	padding:10px;
	border-top:none;
}
.set_label + textarea{margin-top:5px}
input.disabled, input:disabled, textarea:disabled{color:#acacac !important;background:#f5f5f5;opacity:1;}

select{
	border:1px solid #fff;
	border-bottom-color:#c2c2c2;
	height:40px;
	filter:alpha(opacity=0);
	font-family:"Noto Sans KR";
	font-size:16px;
	background-image:url('../img/icon_selectBg.png');
	background-position:right 12px;
	background-repeat:no-repeat;
	background-color:#fff;
	background-size:15px auto;
	cursor:pointer;
	padding:5.5px 0;
	padding-right:20px;
	-webkit-appearance:none;
	-moz-appearance:none;
	-o-appearance:none;
	appearance:none;
	color:#000;
	width:100%;
	font-weight:700;
}
/*.select_on{color:rgba(0,0,0,0.2);}*/
.select_on{color:#000;}
.selct_type01 select{
	width:auto;
	padding-right:20px;
	min-width:85px;
