select::-ms-expand{display:none;}
.text_focus::-ms-clear{display:none;}

input:focus, select:focus{border-bottom-color:#3380ff;outline:none;background-color:#fff}
input:read-only:focus, textarea:read-only:focus, input.readonly:focus{border-bottom-color:#c2c2c2;}

textarea:focus{border-bottom-color:#3380ff;outline:none;}
.selct_type01 select:focus{border-bottom-color:#d9d9d9;}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active{
transition:background-color 5000s ease-in-out 0s;
-webkit-transition:background-color 9999s ease-out;
box-shadow:0 0 0px 1000px white inset !important;
}

select:disabled{color:#acacac;background-image:url('../img/icon_selectBg_off.png');}
/*button*/
.btn{
	font-family:"Noto Sans KR";
	display:inline-block;
	border-width:1px;
	border-style:solid;
	color:#000;
	text-align:center;
	font-size:15px;
	cursor:pointer;
	padding:10px;
	font-weight:400;
	border-radius:3px;
}

/*date type box*/
.date_box{position:relative;}
.date_box:after{content:"";position:absolute;right:0px;top:5px;display:block;width:30px;height:30px;background:url("../img/icon_cal01.png") no-repeat center center;background-size:17px auto;}
.date_box input[type="date"]{color:#fff;}
.date_box input[type="date"] + span{position:absolute;left:0px;top:11px;opacity:.2;font-weight:700;}
.date_box input[type="date"] + label{position:absolute;left:0px;top:11px;opacity:.2;font-weight:700;}
.date_box.on input[type="date"]{color:#000;}
.date_box.on input[type="date"] + span{display:none;}
.date_box.on input[type="date"] + label{display:none;}
.date_box input[type="month"]{color:#fff;}
.date_box input[type="month"] + span{position:absolute;left:0px;top:11px;opacity:.2;font-weight:700;}
.date_box input[type="month"] + label{position:absolute;left:0px;top:11px;opacity:.2;font-weight:700;}
.date_box.on input[type="month"]{color:#000;}
.date_box.on input[type="month"] + span{display:none;}
.date_box.on input[type="month"] + label{display:none;}
.time_box{position:relative;}
.time_box:after{content:"";position:absolute;right:0px;top:8px;display:block;width:30px;height:30px;background:url("../img/icon_time01.png") no-repeat center center;background-size:19px auto;}
.time_box input[type="time"]{color:#fff;}
.time_box input[type="time"] + label{position:absolute;left:0px;top:11px;opacity:.2;font-weight:700;}
.time_box.on input[type="time"]{color:#000;}
.time_box.on input[type="time"] + label{display:none;}
/*check box*/
input[type="checkbox"]{display:inline-block;position:absolute;left:-9999px;width:100%}
input[type="checkbox"] + label{z-index:1;position:relative;display:inline-block;cursor:pointer;line-height:19px;padding:8px;padding-left:30px;color:#333;font-size:14px;}
input[type="checkbox"] + label:before{content:"";display:inline-block;width:20px;height:20px;position:absolute;left:0;top:8px;background-image:url("../img/form_checkOff.png");background-position:center center;background-repeat:no-repeat;background-size:13px auto;border:1px solid #d9d9d9;}
input[type="checkbox"]:checked + label:before{background-image:url("../img/form_checkOn.png");}
input[type="checkbox"]:checked:disabled + label:before{content:"";display:inline-block;width:20px;height:20px;position:absolute;left:0;background-image:url("../img/form_checkOff.png");background-position:center center;background-repeat:no-repeat;background-size:13px auto;border:1px solid #d9d9d9;}
input[type="checkbox"]:disabled + label:before{background-image:url("../img/form_checkOff.png");}

/*check box*/
.check_one input[type="checkbox"] + label{padding:8px;padding-left:30px;font-size:16px;font-weight:500;}
.check_one input[type="checkbox"] + label:before{width:25px;height:23px;background-image:url("../img/form_checkOff1.png");background-size:25px auto;border:none;top:6px;}
.check_one input[type="checkbox"]:checked + label:before{background-image:url("../img/form_checkOn1.png");}
.check_one input[type="checkbox"]:checked:disabled + label:before{width:25px;height:23px;background-image:url("../img/form_checkOn1.png");background-size:25px auto;border:none;top:6px;}
.check_one input[type="checkbox"]:disabled + label:before{width:25px;height:23px;background-image:url("../img/form_checkOff1.png");background-size:25px auto;border:none;top:6px;}

.check_one.only input[type="checkbox"] + label{width:30px;height:30px;padding:0;}
.check_one.only input[type="checkbox"] + label:before{width:100%;height:100%;top:0;}
.check_one.only input[type="checkbox"]:checked + label:before{width:100%;height:100%;top:0;}

.check_one input[type="radio"] + label{padding:8px;padding-left:30px;font-size:16px;font-weight:500;}
.check_one input[type="radio"] + label:before{width:25px;height:23px;background-image:url("../img/form_checkOff1.png");background-size:25px auto;border:none;top:6px;border-radius:0;}
.check_one input[type="radio"]:checked + label:before{background-image:url("../img/form_checkOn1.png");border-radius:0;}
.check_one input[type="radio"]:checked:disabled + label:before{background-image:url("../img/form_checkOn1.png");}
.check_one input[type="radio"]:disabled + label:before{background-image:url("../img/form_checkOff1.png");}
.check_one_list .check_one{display:inline-block;padding-top:10px;}
.check_one_list .check_one + .check_one{padding-left:15px;}
