.radio_btn_box{overflow:hidden;display:table;table-layout:fixed;}
.radio_btn{display:table-cell;overflow:hidden}
.radio_btn input[type="radio"] + label{border:1px solid #3380ff;border-left:none;background:#fff;padding:4px 15px;color:#3380ff;font-size:13px;display:block;}
.radio_btn:first-child input[type="radio"] + label{border-left:1px solid #3380ff;border-radius:4px;border-top-right-radius:0px;border-bottom-right-radius:0px;}
.radio_btn:last-child input[type="radio"] + label{border-radius:4px;border-top-left-radius:0px;border-bottom-left-radius:0px;}
.radio_btn input[type="radio"] + label:before{display:none}
.radio_btn input[type="radio"]:checked + label{background:#3380ff;color:#fff;}

.radio_list_box{overflow:hidden;}
.radio_list_box > *{float:left;}
.radio_list_box > * + *{margin-left:10px}

.check_half_list{overflow:hidden;padding-top:5px;}
.check_half_list > *{float:left;width:50%;padding:5px 0;}

/*switch input*/
.switch{position:relative;display:inline-block;width:42px;height:24px;}
.switch input{opacity:0;width:0;height:0;}
.switch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#cfcfd6;-webkit-transition:.4s;transition:.4s;}
.switch .slider:before{position:absolute;content:"";height:18px;width:18px;left:3px;bottom:3px;background-color:#fff;-webkit-transition:.4s;transition:.4s;}
.switch input:checked + .slider{background-color:#0f0f33;}
.switch input:checked + .slider:before{-webkit-transform:translateX(18px);-ms-transform:translateX(18px);transform:translateX(18px);}
.switch .slider.round{border-radius:17px;}
.switch .slider.round:before{border-radius:50%;}
.switch input:checked + .blue{background-color:#2196F3;}

